.recommend-wrap{
    .recommend-title{
        @include layout(100%, auto);
        padding: 30px 0;
        background-color: $primary-50;
        @include font($ns, 24px);
        text-align: center;

        span{
            font-weight: 700;
            animation: light 1s infinite;
            &:nth-child(2){animation-delay: 0.5s;}
            &:last-child{
                animation: none;
                color: rgb(236, 60, 60);
            }
        }
    }
    .recommend-content{
        margin-bottom: 70px;

        .time-table{
            @include flex(center, flex-start);
            gap: 32px;
            
            .air-way{
                @include flex(center, flex-start);
                gap: 40px;
                @include layout(auto, 150px);
                @include font($ns, 40px);
                text-align: center;

                .en-city{
                    margin-top: 10px;
                    @include font($ns, 20px, #aaa);
                }
            }
            .air-info{
                @include flex(center, flex-start);
                @include font($ns, 24px, $primary);

                .air-time{
                    margin-right: 24px;
                    color: #78b3ff;
                }
            }
        }
        .ticket-info{
            @include font($ns, 24px);
            thead{
                tr{
                    height: 40px;
                    background-color: $primary;
                    
                    
                    th{
                        line-height: 35px;
                        color: #fff;

                        &:first-child{border-top-left-radius: 8px;}
                        &:last-child{border-top-right-radius: 8px;}
                    }
                }
            }
            tbody{
                tr{
                    height: 60px;
                    background-color: $primary-20;

                    td{
                        padding-top: 16px;
                        text-align: center;
                    }
                    .ticket-price{
                        font-weight: 600;
                        color: rgb(236, 60, 60);
                    }
                }
            }
        }
    }
}

@keyframes light{
    0%{color: #fff;}
    50%{color: rgb(236, 60, 60);}
    100%{color: #fff;}
}