.result-wrap{
    @include flex(center, flex-start, column);
    @include layout(100%, calc(100vh - 80px));
    background-color: $primary-20;
    
    .worldcup-result{
        @include layout(100%, auto);
        padding: 30px 0;
        background-color: $primary-50;
        @include font($ns, 24px);
        text-align: center;

        span{
            color: $primary;
            font-weight: 700;
        }
    }
    .result-content{
        @include layout(50%, 50%);
        margin-top: 130px;
    }
    button{
        @include layout(160px, 60px);
        margin-top: 80px;
        background-color: $primary;
        border-radius: 10px;
        border: 0;
        @include font($ns, 16px, #fff);
        transition: all 0.3s;
        cursor: pointer;

        &:hover{
            background-color: $primary-80;
            color: $primary;
            transform: scale(1.1);
        }
    }
}